import Reveal from 'reveal.js/dist/reveal.esm';
import Markdown from 'reveal.js/plugin/markdown/markdown.esm';
import Highlight from 'reveal.js/plugin/highlight/highlight.esm';
import io from 'socket.io-client';

import './styles.scss';

const MultiplexState = {
  None: 'none',
  Presenter: 'presenter',
  Audience: 'audience', 
}

const multiplexState = MultiplexState.Audience

const multiplex = {
  // url: 'http://localhost:1948/',
  url: 'https://reveal-multiplex.glitch.me/',
  id: 'f1d4a50fdce3b0ba',
  secret: multiplexState === MultiplexState.Presenter ? '16152476145581388580' : null,
};

const deck = new Reveal({
  plugins: [Markdown, Highlight],
  hashOneBasedIndex: true,
  hash: true,
  respondToHashChanges: true,

  dependencies: [
    // { src: 'https://reveal-multiplex.glitch.me/socket.io/socket.io.js', async: true },
    // { src: 'https://reveal-multiplex.glitch.me/master.js', async: true },

    //  src: 'https://unpkg.com/reveal-notes-server/client.js', async: true }
  ]
});

deck.initialize().then(() => {
  if (multiplexState !== MultiplexState.None) {
    const {id: socketId, url, secret} = multiplex;
    console.log(io);
    const socket = io(url, {transports: ['websocket']});
    socket.on('connect', () => console.log(socket));
  
    if (multiplexState === MultiplexState.Presenter) {
      
      // Don't emit events from inside of notes windows
      if ( window.location.search.match( /receiver/gi ) ) { return; }
    
      const post = () => {
        const messageData = { secret, socketId, state: deck.getState()};
        socket.emit ('multiplex-statechanged', messageData, ack => console.log('ack', ack));
      };
    
      window.addEventListener( 'load', post);
      [
        'slidechanged',
        'fragmentshown',
        'fragmenthidden',
        'overviewhidden',
        'overviewshown',
        'paused',
        'resumed',
      ].forEach(event => deck.on(event, post));  
    } else {
      socket.on(socketId, data => {
        if (data.socketId !== socketId) { return; }
        if ( window.location.host === 'localhost:1947') { return; }
        deck.setState(data.state);
      });
    }
  }
})

if (module && module.hot) {
  module.hot.accept();
}
